@import url(https://fonts.googleapis.com/css2?family=Play:wght@400;700&display=swap);
body{margin:0;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}.side_by_side{display:flex;flex-direction:row}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#editor{border:1px solid #f05454;border-radius:.25rem;padding:1rem 4rem 2rem 4rem;margin-top:2rem;display:flex;flex-direction:column;align-items:center}#editor h1{margin-bottom:1.5rem}#editor h2{margin-bottom:1rem}#editor #options{display:flex;justify-content:center}#editor #options .option{display:flex;flex-direction:column}#editor .button{font-size:.75rem;background-color:#f05454;color:#e8e8e8;padding:1rem 3rem;border-radius:.25rem;border:1px solid rgba(0,0,0,0);transition:background-color .2s ease-in-out;margin-bottom:1rem;margin-top:1rem;margin-right:1rem}#editor .button:hover{cursor:pointer;background-color:rgba(0,0,0,0);border-color:#f05454}#editor .circle-picker{margin-bottom:1.5rem !important}.flex-container{display:flex;align-items:flex;justify-content:center}.flex-child{flex:1 1;border:1px solid #0a0441}.option{display:flex;flex-direction:column;align-items:center}.inputLabel{font-size:.75rem}.panelInput{height:2.5rem;width:2.5rem;font-size:1.25rem;text-align:center;background-color:#222831;color:#e8e8e8;border:1px solid #30475e;border-radius:.25rem;margin:0 1rem .5rem 1rem;padding-left:1rem}.panelInput:focus{outline:none !important;border-color:#f05454;box-shadow:0 0 .5rem #30475e}.addPadding{padding:1em}
#drawingPanel{display:flex;flex-direction:column;align-items:center}#pixels{margin-top:.75rem;margin-bottom:.75rem;display:flex;flex-direction:column;align-items:center;justify-content:center}.flex-container{display:flex;justify-content:center}.flex-child{flex:1 1;border:1px solid #0a0441;align-items:center;justify-content:center;margin:.75rem}.flex-child:first-child{margin-right:20px;align-items:center;justify-content:center}
.row{display:flex;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}
.pixel{width:1.5rem;height:1.5rem;border-color:gray;border-width:1px;border-style:solid}.pixel:hover{cursor:pointer}
body,html{margin:0;padding:0}body{background:#222831;color:#e8e8e8;display:flex;justify-content:center;align-items:center;font-family:"Play",sans-serif;text-align:center;min-width:-webkit-fit-content;min-width:-moz-fit-content;min-width:fit-content}
